.btn {
    display: inline-block;
    margin-bottom: 0; // For input.btn
    font-size: $btn-font-size;
    font-weight: $btn-font-weight;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
    border: 1px solid transparent;
    white-space: nowrap;

    &.disabled,
    &[disabled],
    fieldset[disabled] & {
        cursor: not-allowed;
        background-color: #ccc;
        border: 0;


    }
}

a.btn {
    &.disabled,
    fieldset[disabled] & {
        pointer-events: none; // Future-proof disabling of clicks on `<a>` elements
    }
}

.btn-default {
    color: #333;
    border-color: #b3b3b3;
    background-color: #fff;
}
.btn-primary {
    @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);
}
.btn-reverse {
    @include button-variant($btn-reverse-color, $btn-reverse-bg, $btn-reverse-border);
}


.btn-lg {
    @include button-size($btn-lg-width, $btn-lg-font, $btn-lg-height, $btn-lg-border-radius)
}

.btn-md {
    @include button-size($btn-md-width, $btn-md-font, $btn-md-height, $btn-md-border-radius)
}

.btn-sm {
    @include button-size($btn-sm-width, $btn-sm-font, $btn-sm-height, $btn-sm-border-radius)
}
