.container {
    margin-left: auto;
    margin-right: auto;
    padding-left:  floor(($grid-gutter-width / 2));
    padding-right: ceil(($grid-gutter-width / 2));
    width: $container-width;
}

.row {
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
    @include clearfix;
}

@include make-grid();
